html {
  box-sizing: border-box;
  margin: 0;
  font-family: 'Lato';
  content: '';
  position: absolute;
  background-size: cover;
  height: 100%;
  width: 100%;
  background-image: url('https://images.unsplash.com/photo-1553029488-a8f28cfd96ec?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=2250&q=80');
}


*, *:before, *:after {
  box-sizing: inherit;
  margin: 0;
}

body {
  font-family: 'Lato';
  color: white;
  position: relative;
}
.background-loader{
  padding: 0;
  background: #33495f8e;
  width: 100vw;
  height: 100vh;
}

.wrapper{
  width: 150px;
  height: 150px;
  position: absolute;
  top: calc(50% - 75px);
  left: calc(50% - 75px);
}

.sun{
  position: absolute;
  bottom: 0px;
  right: 0px;
  width: 85px;
  height: 85px;
  background: #f9db62;
  border-radius: 360px;
  opacity: 1;
  -webkit-animation: sun 2s 0s linear infinite;
          animation: sun 2s 0s linear infinite;
}

.cloud{
  position: absolute;
  bottom: 12px;
  left: 8px;
  z-index: 2;
  opacity: 0;
  -webkit-animation: cloud 2s 0s linear infinite;
          animation: cloud 2s 0s linear infinite;
}

.cloud .cloud1:not(.c_shadow) ul li{
  -webkit-animation: cloudi 2s 0.2s linear infinite;
          animation: cloudi 2s 0.2s linear infinite;
}

.cloud .cloud1:not(.c_shadow):before{
  -webkit-animation: cloudi 2s 0s linear infinite;
          animation: cloudi 2s 0s linear infinite;
}

.cloud_s{
  position: absolute;
  bottom: 70px;
  left: 150px;
  -webkit-transform: scale(0.7,0.7) matrix(-1, 0, 0, 1, 0, 0);
          transform: scale(0.7,0.7) matrix(-1, 0, 0, 1, 0, 0);
  z-index: 1;
  opacity: 0;
  -webkit-animation: cloud_s 2s 0s linear infinite;
          animation: cloud_s 2s 0s linear infinite;
}

.cloud_s .c_shadow{
  -webkit-transform: scale(1.02,1.02);
          transform: scale(1.02,1.02);
}

.cloud_vs{
  position: absolute;
  bottom: 90px;
  left: 30px;
  -webkit-transform: scale(0.5,0.5);
          transform: scale(0.5,0.5);
  z-index: 0;
  opacity: 0;
  -webkit-animation: cloud_vs 2s 0s linear infinite;
          animation: cloud_vs 2s 0s linear infinite;
}

.c_shadow{
  z-index: 4 !important;
  left: -5px;
  bottom: -3px !important;
}

.c_shadow:before{
  background: #33495f !important;
}

.c_shadow ul li{
  width: 60px !important;
  height: 60px !important;
  background: #33495f !important;
  float: left;
  position: absolute;
  bottom: -2px !important;
  border-radius: 360px;
}

.c_shadow ul li:nth-child(2){
  width: 80px !important;
  height: 80px !important;
  background: #33495f !important;
  float: left;
  border-radius: 360px;
  position: absolute;
  bottom: 16px !important;
  left: 25px !important;
}

.c_shadow ul li:nth-child(3){
  width: 70px !important;
  height: 70px !important;
  background: #33495f !important;
  float: left;
  border-radius: 360px;
  position: absolute;
  bottom: 6px !important;
  left: 60px !important;
}

.c_shadow ul li:last-child{
  width: 60px !important;
  height: 60px !important;
  background: #33495f !important;
  float: left;
  border-radius: 360px;
  position: absolute;
  bottom: 0px;
  left: 90px;
}


.cloud1{
  position: absolute;
  bottom: 0px;
  z-index: 5;
}

.cloud1:before{
  content: '';
  position: absolute;
