.haze{
  background: #33495f;
  position: absolute;
  bottom: 50px;
  left: 0px;
  width: 200px;
  height: 45px;
  z-index: 6;
  opacity: 0;
  -webkit-animation: haze 2s 0s linear infinite;
          animation: haze 2s 0s linear infinite;
}

.haze_stripe{
  background: #a3b5c7;
  position: absolute;
  bottom: 75px;
  left: 20px;
  width: 115px;
  height: 10px;
  z-index: 17;
  opacity: 0;
  border-radius: 360px;
  -webkit-animation: haze_stripe 2s 0.2s linear infinite;
          animation: haze_stripe 2s 0.2s linear infinite;
}

.haze_stripe:nth-child(6){
  bottom: 55px;
  -webkit-animation: haze_stripe 2s 0.2s linear infinite;
          animation: haze_stripe 2s 0.2s linear infinite;
}
.haze_stripe:last-child{
  bottom: 35px;
  -webkit-animation: haze_stripe 2s 0.4s linear infinite;
          animation: haze_stripe 2s 0.4s linear infinite;
}

.thunder{
  position: absolute;
  bottom: 100px;
  left: 65px;
  width: 12px;
  height: 12px;
  background: #f9db62;
  -webkit-transform: skew(-25deg);
          transform: skew(-25deg);
  opacity: 0;
  -webkit-animation: thunder 2s 0s linear infinite;
          animation: thunder 2s 0s linear infinite;
}

.thunder:before{
  content: '';
  position: absolute;
  top: 11px;
  left: 0px;
  width: 25px;
  height: 8px;
  background: #f9db62;
}

.thunder:after{
  content: '';
  position: absolute;
  width: 0; 
  height: 0;
  top: 18px;
  right: -14px;
  border-left: 5px solid transparent;
  border-right: 5px solid transparent;
  
  border-top: 20px solid #f9db62;
  -webkit-transform: skew(5deg);
          transform: skew(5deg);
}

.rain{
  position: absolute;
  bottom: 0px;
  left: 25px;
}

.rain ul{
  list-style: none;
  margin: 0;
  padding: 0px;
}

.rain ul li{
  float: left;
  position: absolute;
  bottom: 50px;
  left: 50px;
  margin-left: 20px;
  background: #6ab9e9;
  height: 40px;
  width: 10px;
  border-radius: 360px;
  -webkit-transform: rotate(35deg);
          transform: rotate(35deg);
  opacity: 0;
}

.rain ul li:first-child{
  -webkit-animation: raini 2s 0s linear infinite;
          animation: raini 2s 0s linear infinite;
}

.rain ul li:nth-child(2){
  -webkit-animation: rainii 2s 0.2s linear infinite;
          animation: rainii 2s 0.2s linear infinite;
}

.rain ul li:last-child{
  -webkit-animation: rainiii 2s 0.4s linear infinite;
          animation: rainiii 2s 0.4s linear infinite;
}

.sleet{
  position: absolute;
  bottom: 0px;
  left: 25px;
}

.sleet ul{
  list-style: none;
  margin: 0;
  padding: 0px;
}

.sleet ul li{
  float: left;
  position: absolute;
