  left: 50px;
  margin-left: 20px;
  background: #fff;
  height: 40px;
  width: 10px;
  border-radius: 360px;
  -webkit-transform: rotate(35deg);
          transform: rotate(35deg);
  opacity: 0;
}

.sleet ul li:first-child{
